import React from "react"
import { useCart } from "react-use-cart";
import "bootstrap-icons/font/bootstrap-icons.css";
import img from './qq.gif';
import { useState } from 'react';
import CompConfetti from "./CompConfetti";


export default function CompCart(){
    const [isVisible, setIsVisible] = useState(false);
  
    const {isEmpty,totalUniqueItems,items,totalItems,cartTotal,
    updateItemQuantity,removeItem,emptyCart,}=useCart();
    if (isEmpty){ return (<div className="text-center"><h1 className="text-danger mt-5">Your cart is empty!!!!</h1><img style={{height:'500px',width:'500px',filter:'brightness(80%)',borderRadius:'100%'}} className="img-fluid" src={img} /></div>)
    }
    return(
    <>
<CompConfetti/>
    <section classNmae="py-4 container">
        <div className="row justify-content-center">
            <div className="col-12">
                <h1 className="text-center">Cart Section</h1>
          <h5>Cart Items({totalUniqueItems})- Total Items({totalItems})</h5>
          <table className="table table-light table-hover m-0 ">
            <tbody>
{items.map((item,index)=>{
    return(
    <tr key={index}>
        <td><img src={item.image} style={{height:'100px',width:'100px'}}/></td>
        <td className="fw-bold fs-2"><bold>{item.name}</bold></td>
       
        <td className="fw-bold fs-2">₹.{item.price}</td>
      
        <td>
            <button  onClick={()=>
            updateItemQuantity(item.id,item.quantity -1)
        } className="btn btn-danger ms-2 "><i class="bi bi-dash-circle-fill"></i></button> Qty:{item.quantity}
        <button onClick={()=>
            updateItemQuantity(item.id,item.quantity +1)
        } className="btn btn-success ms-2"><i class="bi bi-plus-circle-fill"></i></button>
        <button onClick={()=>
            removeItem(item.id)
        } className="btn btn-danger ms-2"><i class="bi bi-trash3-fill"></i></button></td>
    </tr>
)
})}

</tbody>

          </table>
            </div>
            <div className="col-auto ms-auto">
                <h2>total price:₹.{cartTotal}</h2>
            </div>
            <div className="col-auto">
                <button onClick={emptyCart} className="btn btn-danger m-2">CLEAR CART</button>
                <button className="btn btn-success m-2" onClick={()=>{
                    alert(`confirm total:₹.${cartTotal}?`)
                    emptyCart()
                    setIsVisible(true)
                    {isVisible && <CompConfetti />}
                }} >Buy</button>
               
               {/* {isVisible && <CompConfetti />} */}
            </div>

        </div>

    </section>
    
    
    </>)
}