// export default CompItemCard;
import React from "react"
import { useCart } from "react-use-cart";
import 'bootstrap/dist/css/bootstrap.min.css';

export default function CompItemCard(props){
    const {addItem}=useCart();
    return(
<>
<div className=" col-md-6 col-lg-4 mx-0 mb-4 ">
    <div className="card p-8 overflow-hidden h-100 shadow">
        <img src={props.image} style={{height:'300px',width:'100%'}} class="card-img-top img-fluid"/>
        <div className="card-body ">
            <h5 class="card-title fs-2 fw-bold">{props.name}</h5>
            <p class="card-text fs-4 fw-bold">Qty:{props.qty}</p>
            <p class="card-text fs-4 fw-bold text-danger">Price:₹.{props.price}</p>
            <div className="d-grid">
            <button onClick={()=>addItem(props.item)} className=" btn btn-success"><i class=" bi bi-cart"></i></button>
            </div>
        </div>
        </div>
    </div>


</>
    )
}