import React from "react";
import { useState } from "react";
// import { Validation } from "../js/Validate";
import { useForm } from "react-hook-form";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from "react-router-dom";
import axios from "axios";
import { useEffect, useRef } from "react"
import { useNavigate, useParams } from "react-router-dom";
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';


//   const schema=yup.object().shape({
//     email:yup.string().email('invalid email').required('enter the email'),
//     password:yup.string().min(8,'minimum 8 chars').max(15,'max only 15 chars').required('enter password')
// })

function CompSignup(){
  // const {register,handleSubmit,formState:{errors}}=useForm({
  //   resolver:yupResolver(schema)
  // });
    const [formData,setFormData]=useState({
              email:'',
              password:'',
              dts:''
    })
    const [errors,setErrors]=useState({})
    const [valid,setValid]=useState(true)
    const nav=useNavigate();
    
  const handleSubmit=(e)=>{
    e.preventDefault()
    let isValid=true;
    let validationErrors={}

    if(formData.email==="" || formData.email===null){
        isValid=false;
        validationErrors.email="email required"
    }else if(!/\S+@\S+\.\S+/.test(formData.email)){

        isValid=false;
        validationErrors.email="email Invalid"
    }
    if(formData.password==="" || formData.password===null){
        isValid=false;
        validationErrors.password="password required"
    }else if(formData.password.length<=8){

        isValid=false;
        validationErrors.password="password atleast 8 chars"
    }
  
    axios.get("http://localhost:4000/Users")
    .then(result=>{
        result.data.map(user=>{
            if(user.email===formData.email ){
              if(user.password===formData.password){
             
              alert("login Successfully")
              nav("/home")}
         } else{
          isValid=false;
         validationErrors.dts="invalid credentials"
         }
      //    if( user.email != formData.email){
      //       isValid=false;
      //       validationErrors.email="wrong email"
      //   }
      //   if( user.password != formData.password){
      //     isValid=false;
      //     validationErrors.password="wrong password"
      // }
    })
    setErrors(validationErrors)
    setValid(isValid)
    })
    setErrors(validationErrors)
    setValid(isValid)
  }
  function showHide(e){
    e.preventDefault();
    if( document.getElementById("pass").type==="password"){
     document.getElementById("pass").type="text"
     document.getElementById("i").className="bi bi-eye-slash-fill"
     
    }else{
     document.getElementById("pass").type="password"
     document.getElementById("i").className="bi bi-eye-fill"
    }
  }
       
        return(
            <>
             <form className="mt-5 p-3 bg-light rounded-3 container border border-none"  onSubmit={handleSubmit} style={{width:'500px'}}>
            <h3 className="text-center">Sign In</h3>
            {valid?<></>:<span className="text-danger">
                {errors.dts}
                {/* {errors.password} */}
                
                </span>}
            <div className="mb-3">
              <label>Email address</label>
              <input
                type="email"
                name="email"
                className="form-control"
                placeholder="Enter email"
                onChange={(event)=>{setFormData({...formData,email:event.target.value})}}
                // {...register('email')} 
              />{valid?<></>:<span className="text-danger">
              {errors.email}
              
              
              </span>}
              {/* <p class="text-danger">{errors.email?.message}</p> */}
            </div>
            <div className="mb-3">
              <label>Password</label>
              <div className="d-flex">
              <input
              id="pass"
                type="password"
                name="password"
                className="form-control"
                placeholder="Enter password"
                onChange={(event)=>{setFormData({...formData,password:event.target.value})}}
                // {...register('password')}
                /> 
                <button className="btn-success" onClick={showHide}><i id="i" class="bi bi-eye-fill"></i></button> 
                </div>
                {valid?<></>:<span className="text-danger">
                
                {errors.password}
                
                </span>}
              
              {/* <p class="text-danger">{errors.password?.message}</p> */}
            </div>
          
            <div className="d-grid">
              <button type="submit"  className="btn btn-success">
                Submit
              </button>
            </div>
            <p className="forgot-password text-center">
              Don't have an account <Link className='text-dark fs-5 fw-bold text-decoration-none' to="/reg"><a className='text-success' href="#">Register here?</a></Link>
            </p>
          </form>
        
    
        
            </>
        )
    }
    
    export default CompSignup;
