import React from 'react'
import { Navbar,Container, Nav } from 'react-bootstrap'
import { Link} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';



export default function CompNav() {
    
        return (
          <>
          <Navbar bg="success" style={{zIndex:'3',position:'sticky',top:'0'}} expand="md">  
    <Container>  
      <Navbar.Brand className=" text-light fs-1 fw-bold" href="#home"><i class="bi bi-cart"></i>Shopping</Navbar.Brand>  
      <Navbar.Toggle aria-controls="basic-navbar-nav" />  
      <Navbar.Collapse id="basic-navbar-nav">  
        <Nav className="ms-auto">  

          {/* <Nav.Link ><Link className='text-dark fs-5 fw-bold text-decoration-none' to="/reg">Register</Link></Nav.Link>   */}
          <Nav.Link ><Link className='text-light fs-5 fw-bold text-decoration-none' to="/home">Home</Link></Nav.Link>  
          <Nav.Link ><Link className='text-light fs-5 fw-bold text-decoration-none' to="/offer">Offers</Link></Nav.Link> 
          <Nav.Link ><Link className='text-light fs-5 fw-bold text-decoration-none' to="/cart"><i class="bi bi-cart-fill"></i></Link></Nav.Link> 
          {/* <Nav.Link ><Link className='text-dark fs-5 fw-bold text-decoration-none' to="/home">Home</Link></Nav.Link> */}
          <Nav.Link name="login" ><Link className='text-light fs-5 fw-bold text-decoration-none' to="/"><i class="bi bi-door-open"></i></Link></Nav.Link>  
        </Nav>  
      </Navbar.Collapse>  
    </Container>  
  </Navbar>  
          
       
         
          </>
        )
    }

