import React from "react"
import 'bootstrap/dist/css/bootstrap.min.css';
import CompItemCard from "./CompItemCard"
import data from "../js/data"
import CompBanner from "./CompBanner";



export default function CompHome(){
    return(
<>
<CompBanner />
<h1 className="text-center text-success mt-3">Fruits</h1>
<section className="py-4 container  ">
<div className=" row justify-content-center">
    {data.products.map((item,index)=>{
        return(
            <CompItemCard image={item.image} name={item.name} qty={item.qty} price={item.price} item={item} key={index} />
        )
    })}
  
</div>
</section>
</>
    )
}