import React from "react";
import { useState } from "react";
// import { Validation } from "../js/Validate";
import { Link } from "react-router-dom";
import { useForm } from "react-hook-form";
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from "axios";
import { useEffect, useRef } from "react"
import { useNavigate, useParams } from "react-router-dom";

import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';

// const schema=yup.object().shape({
//     email:yup.string().email('invalid email').required('enter the email'),
//     password:yup.string().min(8,'minimum 8 chars').max(15,'max only 15 chars').required('enter password'),
//     confirmpassword:yup.string().oneOf([yup.ref('password'),null])
// })

function CompRegister(){
// const {register,handleSubmit,formState:{errors}}=useForm({
//   resolver:yupResolver(schema)
// });

    // axios.post("http://localhost:4000/Users",reg)
    // .then(()=>{
    //     nav("/")
        
    // });
//
const uid=useRef("");
const upw=useRef("");
const nav=useNavigate();

   
    const [formData,setFormData]=useState({
      email:'',
      password:'',
      cpassword:'',
      dts:''
})
const [errors,setErrors]=useState({})
const [valid,setValid]=useState(true)
// const nav=useNavigate();

const handleSubmit=(e)=>{
e.preventDefault()
let reg={
  email:uid.current.value,
  password:upw.current.value

};
let isValid=true;
let validationErrors={}

if(formData.email==="" || formData.email===null){
isValid=false;
validationErrors.email="email required"
}else if(!/\S+@\S+\.\S+/.test(formData.email)){

isValid=false;
validationErrors.email="email Invalid"
}
if(formData.password==="" || formData.password===null){
isValid=false;
validationErrors.password="password required"
}else if(formData.password.length<=8){

isValid=false;
validationErrors.password="password atleast 8 chars"
}
if(formData.password==="" || formData.password===null){
  isValid=false;
  validationErrors.cpassword="password required"
  }else if(formData.password !== formData.cpassword){
  
  isValid=false;
  validationErrors.cpassword="password not match"
  }
  
  if(Object.keys(validationErrors).length===0){
axios.post("http://localhost:4000/Users",reg)
.then(()=>{
  
alert("registeration Successfully")
      nav("/")
  
})
  }
setErrors(validationErrors)
setValid(isValid)
}
function showHide(e){
  e.preventDefault();
  if( document.getElementById("pass").type==="password" && document.getElementById("cpass").type==="password"){
   document.getElementById("pass").type="text"
   document.getElementById("cpass").type="text"
   document.getElementById("i").className="bi bi-eye-slash-fill"
   
  }else{
   document.getElementById("pass").type="password"
   document.getElementById("cpass").type="password"
   document.getElementById("i").className="bi bi-eye-fill"
  }
}



   
    return(
        <>
          <form className="mt-5 p-3 bg-light rounded-3 container border border-none" onSubmit={handleSubmit}    style={{width:'500px'}}>
        <h3 className="text-center">Register</h3>
        {valid?<></>:<span className="text-danger">
                {errors.dts}
                {/* {errors.password} */}
                
                </span>}
        <div className="mb-3">
          <label>Email address</label>
          <input
            type="email"
            name="email"
            className="form-control"
            placeholder="Enter email"
           ref={uid}
           onChange={(event)=>{setFormData({...formData,email:event.target.value})}}
            // {...register('email')} 
          /> {valid?<></>:<span className="text-danger">
          {errors.email}
          {/* {errors.password} */}
          
          </span>}
          {/* <p class="text-danger">{errors.email?.message}</p> */}
        </div>
        <div className="mb-3">
          <label>Password</label>
          <div className="d-flex">
          <input
          id="pass"
            type="password"
            className="form-control"
            placeholder="Enter password"
            name="password"
            // {...register('password')}
            ref={upw}
            onChange={(event)=>{setFormData({...formData,password:event.target.value})}}
          /> 
          <button className="btn-success" onClick={showHide}><i id="i" class="bi bi-eye-fill"></i></button> 

          </div>
          {valid?<></>:<span className="text-danger">
          {/* {errors.dts} */}
          {errors.password}
          
          </span>}
          {/* <p class="text-danger">{errors.password?.message}</p> */}
        </div>
        <div className="mb-3">
          <label>Confirm Password</label>
          <input
          id="cpass"
            type="password"
            name="cpassword"
            className="form-control"
            placeholder="Enter confirm password"
            // {...register('confirmpassword')}
            ref={upw}
            onChange={(event)=>{setFormData({...formData,cpassword:event.target.value})}}
          /> {valid?<></>:<span className="text-danger">
          {/* {errors.dts} */}
          {errors.cpassword}
          
          </span>}
          {/* <p class="text-danger">{errors.confirmpassword?.message}</p> */}
        </div>
        <div className="d-grid">
          <button type="submit"  className="btn btn-success">
            Submit
          </button>
        </div>
        <p className="forgot-password text-center">
        Go to <Link className=' fs-5 fw-bold text-decoration-none' to="/"><a className='text-success' href="#">Sign In page?</a></Link>
        </p>
      </form>
        {/* <div className="container mt-3">
       <form onSubmit={handleSubmit((data)=>{
        console.log(data)
       })} className="p-5 border border-none d-flex flex-column justify-content-center align-items-center ">
       <h1>Register Form</h1>
        <label className="mt-3">Username:</label>
        <input {...register('email')} name="email" className="outline-none" type="text" />
<p class="text-danger">{errors.email?.message}</p>
        <label>Password:</label>
        <input {...register('password')} name="password" type="text" />
        <p class="text-danger">{errors.password?.message}</p>
        <label>Confirm Password:</label>
        <input {...register('confirmpassword')}  type="text" />
        <p class="text-danger">{errors.confirmpassword?.message}</p>
        <button type="submit" class="btn btn-primary" >Sign up</button>
       </form>

       </div> */}
        </>
    )
}

export default CompRegister;